@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Josefin Sans', sans-serif;
}

/* Login Page CSS Code */
.login_container{
    height: 100vh;
    width: 100vw;
    display: grid;
    place-items: center;
    background-color: #202b58;
}


/* Register Page CSS Code */
.Register_container{
    height: 100vh;
    width: 100vw;
    display: grid;
    place-items: center;
    background-color: #202b58;
}


/* Subjects Managements Page CSS Code */
.subjects_area{
  height: 100%;
  width: 100%;
  padding-top: 4rem;
  padding-left: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color:#e5e5e5;
}

.subjects_fcard h1{
   font-size: 2rem;
}

.subjects_fdiv{
    flex: 0 1;
}

.subjects_fdiv h1{
    font-family: 'Josefin Sans', sans-serif;
   font-weight: 600;
}

.subject_sdiv{
    flex: 1 1;
    margin: 2rem;
}

/* Admin DashBoard CSS */
.admin_div{
    padding-left: 4rem;
    padding-top: 4rem;
}

/* For All Buttons */
button{
    color:white;
    padding: 0.35rem 0.7rem;
    background: linear-gradient(90deg, #304185 0%, #19318d 100%);
    margin-right: 2rem; 
    transition: background .6s ease; 
    font-weight: 600;
}

button:hover{
    border: 2px solid #202B58; 
    color: #202b58;
    background: whitesmoke;
}

/* Link Tag */
.link-tag{
    text-decoration: none;
    color: black;
}
.link-tag:hover{
    text-decoration: none;
    color: #0b209c;
}

/* CSS of All Mini Menu */
.link_mini{
    color: black;
    font-size: 28px;
}
.link_mini:hover{
    color: black;
    font-size: 28px;
    text-decoration: none;
    font-weight: bold;
}

.subjects_scard .subjects_search{
    display: flex;
    justify-content: space-between;
}

