    height: 4vh;
    width: 12vw;
    padding: 1rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
} */

.Subject_Details{
    display: flex;
}
.Subject_Details span{
 font-family: 'Courier New', Courier, monospace;
 font-weight: bold;
}
.Subjects{
    display: flex;
}
.Subjects div{
   padding-left: 1rem;
}

/* Add Subject Components */
.addsubject_container{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color:#e5e5e5; 
}

.addsubject-maindiv{
    padding-top: 4rem;
    padding-left: 3rem;
}

.addsubject_fdiv{
    flex: 0 1;
}

.addsubject_fcard{
    height: 4rem;
}
.addsubject_scard{
    margin-top: 2rem;
}

/* Edit Subject Components */
.editsubject_container{
    height: 100vh;
    width: 100vw;
    padding-top: 2rem;
    display: flex;
    flex-direction: column;
    background-color:#e5e5e5; 
}

/* NavBar Css Code */

.tophead{
    display: flex;
    background-color: #202b58;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
    width: 100%;
    height: 65px;
}
.tophead div{
 color: whitesmoke;
}
.brand-logo{
font-size: 2.1rem;
margin: auto;
padding-right: 5rem;
}
.tophead .menulines{
font-size: 1.5rem;
padding: 1rem;
cursor: pointer;
transition: 0.5s;
transition-property: color;
}
.tophead .menulines:hover{
color: grey;
}

.sidenav{
    background-color: whitesmoke;
    position: fixed;
    padding-top: 0px;
    left: 0;
    top: 0;
    z-index: 1;
    overflow-x: hidden;
    height: 100%;
    width: 0;
    transition: all 0.2s;
}

.sidenav a{
    display: block;
    color: #202b58;
    width: 100%;
    line-height: 3rem;
    text-decoration: none;
    padding-left: 2rem;
    box-sizing: border-box;
    transition: 0.5s;
    transition-property: background;
}

.sidenav a:hover{
    background: #202b58;
    text-decoration: none;
    color: whitesmoke;
}

.sidenav .side_icons{
margin-right: 10px;
}

.closebtn {
    position: absolute;
    top: 20px;
    right: 25px;
    font-size: 25px;
}

/* Side Menu Css */
.nav-toggle:hover{
    background-color: #414b71;
}

/* PreLoader CSS Code */
.preloader{
    height: 100%;
    width: 100%;
    display: grid;
    place-items: center;
    padding-right: 5.8rem;
}

/* Media Query For Mobile Devices */
@media (max-width: 768px){
    .brand-logo{
        font-size: 2.1rem;
        margin: auto;
        padding-left: 7rem;
        }
        .main_btn{
            font-size: 13px;
            padding: 0.4rem;
        }
        .input-area{
           margin-left: 3rem;
        }
        .preloader{
            padding-right: 0rem;
        }
}
